.UploadTorrent {
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border: 5px solid rgba(190, 189, 189, 0.4);
    border-radius: 2px;
    padding: 10px 20px;
}

.UploadTorrent * {
    border: none;
    border-radius: 1px;
}


@media all and (max-width: 400px) {
    .UploadTorrent {
        padding: unset;
        border: unset;
    }
    .UploadTorrent>* {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .UploadTorrent>*::after {
        display: none;
    }
}